import React from 'react'

const Logo = ({ name, svg_file, onClick }) => (
	<div className="col-xs-4">
		<a
			href="#"
			className="logoItem img-thumbnail"
			onClick={onClick}
			>
			<img
				className="img-responsive"
				src={svg_file}
				title={name}
				alt={name}
			/>
		</a>
	</div>
)

export default Logo


