import { fetchLogos } from '../actions'
const appInsertLogo = window.app.insertLogo;

const logo = (state = {}, action) => {
	switch(action.type) {
		case 'REQUEST_SEARCH_LOGOS':
			
		default:
			return state
	}
}

const logos = (state = [], action) => {
	switch(action.type) {
		case 'RECEIVE_SEARCH_LOGOS':

			var logos = action.results.data

			return [
				...state,
				...logos
			]

		case 'RECEIVE_LOAD_MORE_LOGOS':
			return [
				...state,
				...logos
			]
		case 'LOAD_MORE':
			return state
		default:
			return state
	}
}

const results = (
	state = {
		logos: [],
		nextPage: null
	},
	action
) => {
	switch(action.type) {
		case 'REQUEST_SEARCH_LOGOS':
			return state;
		case 'RECEIVE_SEARCH_LOGOS':
			return {
				...state,
				nextPage: action.results.next_page_url,
				logos: logos(state.logos, action)
			}
		case 'INSERT_LOGO':
			let logo = state.logos.find((value, index) => {
				if(value.id === action.id) {
					return value
				}
			});

			appInsertLogo(logo)

			return state
		default:
			return state
	}
}

export default results


